<template>
  <div class="uk-card uk-card-default uk-card-small">
    <div class="uk-card-header app--card-header">
      <div uk-grid>
        <div class="uk-width-auto">
          <div class="app--card-header__back">
            <router-link :to="{ name: 'websites' }">
              <font-awesome-icon icon="chevron-left"></font-awesome-icon>
            </router-link>
          </div>
        </div>
        <div class="uk-width-expand">
          <div class="app--card-header_title">
            <h3>{{ title }}</h3>
          </div>
        </div>
      </div>
    </div>
    <div class="uk-card-body">
      <div class="uk-margin" v-if="edit">
        <label class="uk-form-label">Kode</label>
        <el-input v-model="input.code" :disabled="formCode"></el-input>
      </div>
      <div class="uk-margin">
        <label class="uk-form-label">Nama</label>
        <el-input v-model="input.name"></el-input>
      </div>
      <div class="uk-margin">
        <label class="uk-form-label">Harga Jual</label>
        <el-input v-model="input.price"></el-input>
      </div>
      <div class="uk-margin">
        <label class="uk-form-label">Harga Beli</label>
        <el-input v-model="input.price_buy"></el-input>
      </div>
      <div class="uk-margin">
        <label class="uk-form-label">Stok</label>
        <el-input v-model="input.stock"></el-input>
      </div>
    </div>
    <div class="uk-card-footer uk-text-right">
      <el-button type="primary" @click="save">SAVE</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      edit: false,
      title: "Produk Baru",
      formCode: true,
      input: {
        name: "",
        price: "",
        price_buy: "",
        stock: ""
      },
      auth: {
        db_name: "skripsi_webhade_tenant",
        db_user: "root_admin_panel",
        db_pass: "qazwsxedc1234567"
      }
    };
  },

  created() {
    if (this.$route.params.id) {
      this.edit = true;
      this.title = "Edit Produk";

      this.getProduct();
    }
  },

  methods: {
    async getProduct() {
      this.__startLoading();

      this.error = false;
      this.errorMessage = "";

      try {
        let res = await this.$service.product.find(this.$route.params.id);

        this.input = res.data.data;
      } catch (err) {
        this.__handleError(this, err, true);
      }

      this.__stopLoading();
    },
    save() {
      if (this.edit) {
        this.update();
      } else {
        this.store();
      }
    },
    async store() {
      let payload = {
        ...this.input,
        authTenant: JSON.parse(window.localStorage.getItem("authTenant"))
      };

      this.__startLoading();

      this.error = false;
      this.errorMessage = "";

      try {
        let res = await this.$service.product.create(payload);

        this.$notify({
          title: "SUCCESS",
          message: res.data.message,
          type: "success"
        });

        this.$router.push({ name: "websites" });
      } catch (err) {
        this.__handleError(this, err, true);
      }

      this.__stopLoading();
    },
    async update() {
      let payload = {
        ...this.input,
        authTenant: JSON.parse(window.localStorage.getItem("authTenant"))
      };

      this.__startLoading();

      this.error = false;
      this.errorMessage = "";

      try {
        let res = await this.$service.product.update(
          this.$route.params.id,
          payload
        );

        this.$notify({
          title: "SUCCESS",
          message: res.data.message,
          type: "success"
        });

        this.$router.push({ name: "websites" });
      } catch (err) {
        this.__handleError(this, err, true);
      }

      this.__stopLoading();
    }
  }
};
</script>

<style lang="scss" scoped>
.req-doc {
  padding-top: 10px;
  .btn-r {
    padding-right: 5px;
    padding-left: 5px;
  }
}
</style>
